<style lang="less">
    @import './hub.less';
</style>
<template>
    <div class="hub-main">
        <div class="hub-form">
            <Form ref="formHub" :model="formHub" :rules="ruleHub" :label-width="80">
                <FormItem label="hub host" prop="host">
                    <Input type="text" v-model="formHub.host" placeholder="ip:port"></Input>
                </FormItem>
                <FormItem>
                    <a @click="resetSetting">{{$t('page_hub.btn.reset')}}</a>&nbsp;&nbsp;&nbsp;&nbsp;
                    <Button :disabled="saveBtn.disabled" style="width:100px;" type="primary" @click="handleSubmit('formHub')">{{saveBtn.text}}</Button>
                </FormItem>
            </Form>
        </div>
    </div>
</template>
<script>
    import util from '../../libs/util.js';
    export default {
        name:'hub',
        data(){
            const validateIP = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(this.$t('page_hub.validate.null')));
                }else if(util.pattIp.test(value)){
                    callback(new Error(this.$t('page_hub.validate.format')));
                }
                else
                {
                    callback();
                }
            };
            return {
                saveBtn:{
                    text:this.$t('page_hub.btn.save'),
                    disabled:false,
                },
                formHub: {
                    host: ''
                },
                ruleHub: {
                    host: [
                        { validator: validateIP, trigger: 'blur' }
                    ],
                }
            };
        },
        methods: {
            init(){
                this.formHub.host=util.socketHost;
            },
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        localStorage.setItem('wallet_hub_host',this.formHub.host);
                        window.location.reload();
                    } else {
                        this.$Message.error('Fail!');
                    }
                });
            },
            resetSetting()
            {
                localStorage.setItem('wallet_hub_host','');
                window.location.reload();
            }
            
        },
        mounted() {
            this.init();

        }
    };
</script>


